import React from 'react';
import {observer} from 'mobx-react';
import AppleItem from './AppleItem';
import '../styles/appleBasket.scss';

@observer
class AppleBusket extends React.Component {


    /** */
    getAppleItem() {
        let data = [];
        this.props.store.apples.forEach(apple => {
            if (!apple.isEaten) {
                data.push( <AppleItem apple={apple} eatApple={this.props.store.eatApple} key={apple.id}/> )
            }
        });

        if(!data.length) data.push(<div className="empty-tip" key="empty"></div>);

        return data;
    }

    render(){
        let { status, isPicking, buttonText, pickApple  } = this.props.store;
        let {
            appleNow: {quantity:notEatenQuantity,weight:notEatenWeight},
            appleEaten: {quantity:EatenQuantity,weight:EatenWeight}
        } = status;


        return (
            <div className="appleBusket">
                <div className="title"></div>

                <div className="stats">
                    <div className="section">
                        <div className="head"></div>
                        <div className="content">{notEatenQuantity}{notEatenWeight}
                        </div>
                    </div>
                    <div className="section">
                        <div className="head"></div>
                        <div className="content">{EatenQuantity}{EatenWeight}</div>
                    </div>
                </div>

                <div className="appleList">
                    { this.getAppleItem() }
                </div>

                <div className="btn-div">
                    <button  className={isPicking ? 'disabled' : ''}  onClick={() => pickApple() } >{buttonText}</button>
                </div>
            </div>
        );
    }
}



export default AppleBusket;


